import React, { useState } from "react";
import classes from "./Hero.module.css";
import { motion, AnimatePresence } from "framer-motion";
import Carous from "../Carousel/Carous";
import InitialStep from "../Steps/InitialStep";
import CarouselContainer, { CarouselTest } from "../Carousel/CarouselTest";

function Hero() {
  const [intro, setIntro] = useState(true);
  const [introtwo, setIntrotwo] = useState(true);
  const [introthree, setIntrothree] = useState(false);
  const [introfour, setIntrofour] = useState(false);
  setTimeout(() => {
    setIntro(false);
  }, 3000);

  setTimeout(() => {
    setIntrotwo(false);
  }, 2300);

  setTimeout(() => {
    setIntrothree(true);
    setIntrofour(true);
  }, 4500);
  return (
    <div className={classes.home}>
      <CarouselContainer>
        <CarouselTest>
          <div className={classes.titleContainer}>
            <AnimatePresence>
              {intro && (
                <motion.div
                  key={"intro1"}
                  initial={{ x: "-8%", opacity: 0 }}
                  animate={{ x: "0%", opacity: 1 }}
                  transition={{ duration: 0.8, ease: "easeOut", delay: 0 }}
                  className={classes.title}
                  exit={{ x: "-20%", opacity: 0 }}
                >
                  hello,
                </motion.div>
              )}

              {introtwo && (
                <motion.div
                  key={"intro2"}
                  initial={{ x: "-8%", opacity: 0 }}
                  animate={{ x: "0%", opacity: 1 }}
                  transition={{ duration: 0.9, ease: "easeInOut", delay: 1.1 }}
                  className={classes.titlebot}
                  exit={{ x: "-20%", opacity: 0 }}
                >
                  welcome to my website
                </motion.div>
              )}
            </AnimatePresence>
            {introthree && (
              <motion.div
                initial={{ x: "-8%", opacity: 0 }}
                animate={{ x: "0%", opacity: 1 }}
                transition={{ duration: 0.8, ease: "easeOut" }}
                className={classes.title}
                exit={{ x: "-20%", opacity: 0 }}
              >
                I'm Christian Rosa
              </motion.div>
            )}
            {/* Why did i need to make two tags when I could've used one?? yucky */}
            {introfour && (
              <motion.div
                initial={{ x: "-10%", opacity: 0 }}
                animate={{ x: "0%", opacity: 1 }}
                transition={{ duration: 1.3, ease: "easeInOut", delay: 1 }}
                className={classes.titlebot}
                exit={{ x: "-20%", opacity: 0 }}
              >
                and i'm an aspiring Front-end Developer
              </motion.div>
            )}
          </div>
        </CarouselTest>
        <CarouselTest>
          <div className={classes.titleContainer}>a</div>
        </CarouselTest>
        <CarouselTest>
          <div className={classes.titleContainer}>b</div>
        </CarouselTest>
        <CarouselTest>
          <div className={classes.titleContainer}>c</div>
        </CarouselTest>
      </CarouselContainer>
      {/* <Carous /> */}
      <div className={classes.absolute}>
        <InitialStep />
        <InitialStep />
        <InitialStep />
        <InitialStep />
        <InitialStep />
        <InitialStep />
        <InitialStep />
        <InitialStep />
        <InitialStep />
        {/* Nine as default */}
      </div>
    </div>
  );
}

export default Hero;
